@import "styles/variables.scss";

.singer {
  @include base-frame;

  :global {
    .mobile-div {
      @include mobile-frame;
      background-color: #fbeadd;
      position: relative;
    }

    .notes {
      position: absolute;
      width: 100%;
      height: 100%;
    }

    .singer {
      position: absolute;
      top: -150px;
      width: 100%;
    }

    .tone {
      z-index: 3;
      position: absolute;
      width: 100%;
      margin: {
        top: -30px;
      }
    }

    .main-div {
      z-index: 1;
      flex: 1;
      width: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;
    }

    .slider {
      margin: {
        top: 15%;
        left: 70%;
      }
      width: 10px;
      flex: 1;
      background-color: #a2bbb5;
      display: flex;
      flex-direction: column;
      align-items: center;
      position: relative;

      .top {
        background-color: $colorGreenLight;
        width: 26px;
        height: 26px;
        border-radius: 13px;
        margin: {
          top: -13px;
        }
      }

      .cursor {
        position: absolute;
        width: 200px;
      }
    }

    .pop-up {
      z-index: 2;
      margin: {
        top: 10px;
      }
      width: 100%;
      height: 150px;
      background-image: url(~static/images/minimum-popup.png);
      background-size: contain;
      background-position: center;
      background-repeat: no-repeat;
      display: flex;
      flex-direction: column;
      justify-content: flex-end;
      align-items: center;

      & > :last-child {
        margin: {
          bottom: 15px;
        }
      }

      & > .text {
        font-size: 22px;
        color: white;

        .hight-light {
          color: #e9e950;
          margin: {
            right: 5px;
          }
        }
      }
    }

    .singer-text-bold {
      @extend %bold;
      margin: {
        left: 5px;
        right: 5px;
      }
    }

    .tone-text {
      z-index: 4;
      margin: {
        top: 135px;
      }
      @extend %medium;
      font-size: 22px;
      color: #635f76;
    }

    .fine-tune {
      z-index: 4;
      width: 60%;
      margin: {
        top: 20px;
        bottom: 5px;
      }
      font-size: 23px;
    }

    .next {
      z-index: 4;
      width: 60%;
      margin: {
        bottom: 30px;
      }
      background-color: $colorGreenDark;
      font-size: 23px;
    }

    .transparent {
      opacity: 0;
    }

    .hidden {
      display: none;
    }
  }
}
