@import "styles/variables.scss";

.chooseEar {
  display: flex;

  :global {
    .text-wrapper {
      display: flex;
      flex-direction: column;
      text-align: center;
      margin-bottom: 49px;
    }

    .dot {
      margin: 23px 0;
    }

    @mixin btn($backColor) {
      background-color: $backColor;
      width: 100px;
      height: 100px;
      border-radius: 50px;
      font-size: 23px;
      text-align: center;
      line-height: 100px;
      color: white;
    }

    .button-wrapper {
      display: flex;
      flex-direction: row;
    }

    .left-button {
      @include btn(#e7812e);
      margin-right: 47px;
    }

    .right-button {
      @include btn($colorGreenLight);
    }
  }
}

.invisible {
  display: none;
}
