@import "styles/variables.scss";

.instruction {
  @include base-frame;

  :global {
    .mobile-div {
      @include mobile-frame;
      align-items: stretch;
    }

    .top-div {
      flex: 1;
      background-color: #1e538a;
      display: flex;
      flex-direction: column;
      align-items: center;

      & > .title {
        margin: {
          top: 10vh;
          left: 40px;
          right: 40px;
        }
        font-size: 24px;
        color: white;
        text-align: center;
      }

      & > .sub-title {
        margin: {
          top: 10px;
          left: 25px;
          right: 25px;
        }
        font-size: 18px;
        color: white;
        text-align: center;
        opacity: 0.5;
      }

      .step-images {
        margin: {
          top: 20px;
        }
        display: flex;
        flex-direction: column;
        align-items: center;

        & > .images {
          display: flex;
          flex-direction: row;

          & > :not(:first-child) {
            margin: {
              left: 10px;
            }
          }

          & > .item {
            display: flex;
            flex-direction: column;
            align-items: center;

            & > .image {
              width: $mobile-max-width / 4;
              height: $mobile-max-width / 4;
              background-size: contain;
              background-repeat: no-repeat;
              background-position: center;
            }

            & > .image1 {
              background-image: url(~static/images/instruction-step-1.png);
            }

            & > .image2 {
              background-image: url(~static/images/instruction-step-2.png);
            }

            & > .image3 {
              background-image: url(~static/images/instruction-step-3.png);
            }

            & > .title {
              font-size: 15px;
              color: white;
              margin: {
                top: 5px;
              }
            }
          }
        }
      }

      .main-img {
        margin: {
          top: 10px;
          left: 15px;
          bottom: -25px;
        }
        flex: 1;
        width: 70%;
        background-size: contain;
        background-repeat: no-repeat;
        background-position: bottom;
        background-image: url(~static/images/instruction-girl.png);
      }
    }

    .bottom-div {
      margin: {
        left: 30px;
        right: 30px;
      }
      padding: {
        top: 20px;
        bottom: 20px;
      }
      display: flex;
      flex-direction: column;
      align-items: stretch;

      & > :not(:first-child) {
        margin: {
          top: 10px;
        }
      }
    }
  }
}
