@import "styles/variables.scss";

.preScreen {
  @include base-frame;

  :global {
    .mobile-div {
      @include mobile-frame;
      background-color: #fbeadd;
    }

    .finished {
      line-height: 100vh;
      text-align: center;
    }

    .frame {
      width: 100%;
      height: 100%;
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }

    .main-button {
      align-self: stretch;
      margin: {
        left: 50px;
        right: 50px;
        bottom: 30px;
      }
    }

    .text {
      font-size: 23px;
      color: #635f76;
      line-height: 30px;
      text-align: center;
    }

    .green {
      @extend %medium;
      color: $colorGreenLight;
    }

    .yellow {
      @extend %medium;
      color: #e7812e;
    }
  }
}

.invisible {
  display: none;
}
