@import "styles/variables.scss";

.firstEar {
  display: flex;

  :global {
    .container {
      flex: 1;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }

    @mixin btn($backColor) {
      background-color: $backColor;
      width: 40px;
      height: 40px;
      border-radius: 20px;
      font-size: 20px;
      text-align: center;
      line-height: 40px;
      color: white;
      margin: 20px 0;
    }

    .first-button {
      @include btn(#e7812e);
    }

    .second-button {
      @include btn($colorGreenLight);
    }
  }
}

.invisible {
  display: none;
}
