@import "styles/variables.scss";

.signUp {
  @include base-frame;

  :global {
    .mobile-div {
      @include mobile-frame;
      @include background-img;
    }

    .back {
      position: absolute;
      top: 30px;
      left: 20px;
      width: 20px;
      height: 30px;
      background-size: contain;
      background-image: url(~static/images/back-arrow.png);
    }

    .icon {
      $logo-height: 18vh;
      background-color: $colorBlue;
      width: $logo-height;
      height: $logo-height;
      border-radius: $logo-height / 2;
      margin: {
        top: 50px;
      }
    }

    .steps {
      align-self: stretch;
      margin: {
        top: 20px;
        left: 30px;
        right: 30px;
      }
      display: flex;

      & > div {
        width: 100%;
      }

      .hidden {
        display: none;
      }

      .staggered {
        flex: 1;
        display: flex;
        flex-direction: column;

        & > .title {
          font-size: 24px;
          color: $colorBlue;
          line-height: 40px;
        }

        .inputs {
          margin: {
            top: 10px;
          }

          & > :not(:first-child) {
            margin: {
              top: 10px;
            }
          }
        }

        .button {
          width: 56px;
          height: 56px;
          border-radius: 28px;
          background-color: $colorBlue;
          @include next-arrow-img;
          align-self: flex-end;
          margin: {
            top: 10px;
          }
        }
      }
    }

    .bottom-div {
      position: absolute;
      bottom: 30px;
      width: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;
    }

    @mixin tos-font {
      @extend %light;
      font-size: 14px;
    }

    .tos {
      @include tos-font;
      color: #515869;
    }

    .tos-high-light {
      margin: {
        top: 2px;
      }
      @include tos-font;
      color: $colorBlue;
      .inner-tos {
        color: #515869;
        margin: {
          left: 5px;
          right: 5px;
        }
      }
    }
  }
}
