@import "styles/variables.scss";

.splash {
  position: relative;
  display: flex;

  :global {
    .vjs-video {
      background-color: transparent;
    }

    .button {
      position: absolute;
      bottom: 30px;
      left: 10%;
      background-color: transparent;
      width: 80%;
      height: 80px;
    }
  }
}

.invisible {
  display: none;
}
