@import "styles/variables.scss";

.howMany {
  display: flex;

  :global {
    .title {
      margin: {
        bottom: 40px;
      }
      text-align: center;
    }

    .button-wrapper {
      display: flex;
      flex-direction: row;
      flex-flow: wrap;
      justify-content: space-between;
      width: 281px;
    }

    .button {
      background-color: $colorGreenLight;
      width: 132px;
      height: 132px;
      border-radius: 66px;
      text-align: center;
      font-size: 62px;
      color: white;
      line-height: 132px;
      margin-bottom: 19px;
    }
  }
}

.invisible {
  display: none;
}
