@import "styles/variables.scss";

.email {
  display: flex;

  :global {
    .message-div {
      margin: {
        top: 20px;
      }
      opacity: 0;
      display: flex;
      flex-direction: column;

      .text {
        font-size: 18px;
        color: #51586a;
      }

      .buttons {
        margin: {
          top: 10px;
        }
        display: flex;

        & > :not(:first-child) {
          margin: {
            left: 20px;
          }
        }

        .radio-button {
          display: flex;
          align-items: center;
          & > .radio {
            width: 40px;
            height: 40px;
            background-size: contain;
            background-image: url(~static/images/radio-button.jpg);
            margin: {
              right: 5px;
            }
          }
          & > .selected {
            background-image: url(~static/images/radio-button-selected.jpg);
          }
        }
      }
    }

    .message-div-appear {
      opacity: 1;
      transition: opacity 1s;
    }
  }
}
