@import "styles/variables.scss";

.tonePlay {
  background-color: $colorGreenLight;

  :global {
    .content {
      flex: 1;
      height: 100vh;
      margin: {
        left: 30px;
        right: 30px;
      }
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }
    .message {
      color: white;
      font-size: 23px;
      margin-top: 20px;
      text-align: center;
    }
    .progress-bar {
      background-color: #fbeadd;
      width: 100%;
      height: 16px;
      border-radius: 8px;
      overflow: hidden;
      display: flex;

      .progress {
        background-color: #238686;
        animation-name: playTone;
        animation-duration: 5s;
        animation-timing-function: linear;
        animation-fill-mode: forwards;
      }
    }
  }
}

.invisible {
  display: none;
}
