@import "styles/variables.scss";

.scInput {
  display: flex;
  flex-direction: column;
  justify-items: stretch;

  :global {
    .title {
      font-size: 13px;
      color: #cccccc;
    }

    .input {
      margin: {
        top: 5px;
      }
      background-color: transparent;
      border-width: 0px;
      color: #51586a;
      font-size: 20px;

      &:focus {
        outline-width: 0px;
      }
    }

    .line {
      margin: {
        top: 3px;
      }
      background-color: #cccccc;
      height: 1px;
    }

    .error-text {
      color: #ff7e00;
    }

    .error-line {
      background-color: #ff7e00;
    }
  }
}
