@import "styles/variables.scss";

.splash {
  @include base-frame;

  :global {
    .mobile-div {
      @include mobile-frame;
      @include background-img;
    }

    .logo {
      $logo-height: 18vh;
      background-color: $colorBlue;
      width: $logo-height;
      height: $logo-height;
      border-radius: $logo-height / 2;
      margin: {
        top: 15vh;
      }
    }

    .title {
      margin: {
        top: 30px;
      }
      @extend %bold;
      font-size: 30px;
      color: $colorBlue;
      text-align: center;
    }

    .bottom-div {
      position: absolute;
      bottom: 0px;
      width: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;
    }

    .get-started {
      align-self: stretch;
      margin: {
        left: 30px;
        right: 30px;
        bottom: 15px;
      }
      background-color: $colorBlue;
    }

    .have-account {
      margin: {
        bottom: 15px;
      }
      line-height: 50px;
      vertical-align: middle;
      font-size: 18px;
      display: flex;
      flex-direction: row;
      .login {
        margin: {
          left: 5px;
        }
        @extend %medium;
      }
    }
  }
}
