.audioTest {
  max-width: 400px;
  width: 90vw;
  margin: 10px;
  display: flex;
  flex-direction: column;

  h1 {
    font-size: 1.5em;
    margin-bottom: 20px;
  }

  h2 {
    font-size: 1em;
    margin-bottom: 10px;
  }

  :global {
    .content {
      & > div {
        margin-bottom: 20px;
      }
    }

    .dropdown {
      width: 100%;
      height: 30px;
      font-size: 1em;
      cursor: pointer;
    }

    .slider {
      width: 100%;
      margin-bottom: 20px;
    }

    .volume-buttons {
      display: flex;
    }

    .button {
      width: 100%;
      height: 30px;
      font-size: 1em;
      cursor: pointer;
    }
  }
}
